<template>
  <div id="shopping_cart">
    <h1 style="padding-top: 100px;padding-left:400px;" v-if="!isLogin">请先登录哦~</h1>
    <h1 style="padding-top: 100px;padding-left:400px;" v-if="(items.length===0 && isLogin)">购物车为空~</h1>
    <div class="order-row row" v-if="isLogin" v-for ="(item,index) in items">
      <div class="col-md-9">
        <BookOrder :name="item.book.bookname" :receiver="item.receiver" :imagePath="item.book.imagePath" :price="item.book.price" :amount="item.amount" :address="item.address" :isPay="false"/>
      </div>
      <div class="col-md-3">
        <div class="btn btn-success" @click="pay(item,index)">付款</div>
        <div class="btn btn-danger" @click="remove(item,index)">删除订单</div>
        <div class="btn btn-info">修改订单</div>
      </div>
    </div>
  </div>
</template>

<script>
  import BookOrder from "./BookOrder"
    export default {
        name: "shopping-cart",
        data(){
            return{
              items: [
              ],
              isLogin:false,
              currentUser:{
              },
            }
        },
        mounted(){
          let that = this;
          $.ajax({
            type:"get",
            url:"/api/profile",
            success:function(data){
              if(data==="false"){
                that.isLogin = false;
              }else{
                that.isLogin = true;
                that.currentUser = JSON.parse(data);
                $.ajax({
                  type:"get",
                  url:"/api/shoppingcart",
                  success:function(data){
                    if(data==="false"){
                      alert("获取购物车数据失败!");
                    }else{
                      that.items = JSON.parse(data);
                      for (let i=0;i<that.items.length;i++){
                        //默认收件人为用户名
                        that.items[i].receiver = that.currentUser.username;
                      }
                    }
                  },
                  error:function (data) {
                    alert("服务器错误!获取购物车信息失败!");
                  }
                });
              }
            }
          });
        },
        methods:{
          pay:function (item,index) {
            let that = this;
            $.ajax({
              type:"post",
              url:"/api/pay",
              data:{"book_id":item.book.id,"user_id":that.currentUser.id,"amount":item.amount,"receiver":item.receiver,"address":item.address},
              success:function(data){
                if(data==="success"){
                  alert("付款成功!");
                  that.items.splice(index, 1);
                }else{
                  alert("购买失败!");
                }
              },
              error:function () {
                alert("服务器错误!");
              }
            });
          },
          remove: function (item,index) {
            let that = this;
            $.ajax({
              type:"post",
              url:"/api/shoppingcart",
              data:{"method":"remove","id":item.book.id},
              success:function(data){
                if(data==="success"){
                  alert("删除成功!");
                  that.items.splice(index, 1);
                }else{
                  alert("删除失败!");
                }
              },
              error:function () {
                alert("服务器错误!");
              }
            });
          }
        },
        components:{BookOrder}
    }
</script>

<style scoped>
#shopping_cart{
  padding-left:60px;
  padding-right: 60px;
  margin-top: 60px;
}

#shopping_cart .order-row .col-md-3{
  padding-top:80px;
}

#shopping_cart .order-row .col-md-3 .btn{
  border-radius: 0px;
  border:0px;
  display: block;
  margin-top:1px;
  width:100px;
}
</style>



